import React, { CSSProperties } from "react";
import { Anchor, Container, Label, Wrapper } from "./styles";
import { Theme } from "../../assets/theme";

interface Props {
  label: string;
  onClick: () => void;
  flat?: boolean;
  labelColor?: "white" | "red" | "black";
  wrapperStyle?: CSSProperties;
  anchor?: string;
}

const Button: React.FC<Props> = ({
  label,
  flat,
  labelColor,
  onClick,
  wrapperStyle,
  anchor,
}) => {
  const handleOnClick = () => {
    return onClick();
  };

  const handleLabelColor = (): string => {
    switch (labelColor) {
      case "white":
        return Theme.white;
      case "red":
        return Theme.red;
      default:
        return Theme.black;
    }
  };

  const render = () => {
    if (anchor)
      return (
        <Anchor href={anchor}>
          <Container flat={!!flat}>
            <Label labelColor={handleLabelColor()}>{label}</Label>
          </Container>
        </Anchor>
      );

    return (
      <Wrapper onClick={handleOnClick} style={wrapperStyle}>
        <Container flat={!!flat}>
          <Label labelColor={handleLabelColor()}>{label}</Label>
        </Container>
      </Wrapper>
    );
  };

  return render();
};

export default Button;
