import React, { CSSProperties } from "react";
import { Container } from "./styles";

interface Props {
  value: string;
  align: "left" | "center" | "right";
  main?: boolean;
  colors?: boolean;
  style?: CSSProperties;
}

const Title: React.FC<Props> = ({ value, align, main, colors, style }) => {
  return (
    <Container align={align} main={main} colors={colors} style={style}>
      <span>{value}</span>
    </Container>
  );
};

export default Title;
