import React, { CSSProperties } from "react";
import { Container, Span } from "./styles";
import Constants from "../../assets/constants";

interface Props {
  value: string;
  align: "left" | "center" | "right";
  // eslint-disable-next-line no-magic-numbers
  fontWeight?: 200 | 600 | 800;
  colors?: boolean;
  withDash?: boolean;
  containerStyle?: CSSProperties;
  labelStyle?: CSSProperties;
}

const Text: React.FC<Props> = ({
  value,
  align,
  fontWeight,
  withDash,
  colors,
  containerStyle,
  labelStyle,
}) => {
  const handleFontWeight = (): number => {
    switch (fontWeight) {
      case Constants.fonts.light:
        return Constants.fonts.light;
      case Constants.fonts.medium:
        return Constants.fonts.medium;
      case Constants.fonts.bold:
        return Constants.fonts.bold;
      default:
        return Constants.fonts.normal;
    }
  };

  return (
    <Container align={align} style={containerStyle}>
      <Span
        weight={handleFontWeight()}
        withDash={!!withDash}
        colors={!!colors}
        style={labelStyle}
      >
        {value}
      </Span>
    </Container>
  );
};

export default Text;
